import actionlist from "@/components/actionlist.vue"
export default {
	data() {
		return {
			tab:0,//行动，围观，收藏
			list:[],
			userInfo:uni.getStorageSync('userInfo'),
			userId:'',
			total:'',
			lookerList:[],
			pushId:'',
			lookTotal:'',
		}
	},
	onShow() {
		
	},
	onLoad(option) {
		this.userId = option.userId;
		this.pushId = option.pushId;
		console.log(option.userId);
		this.getCardList();
		this.getLookerList();
	},
	methods: {
		getCardList(){
			this.xd_request_post(this.xdServerUrls.xd_pushByUserIdList,{
				token:uni.getStorageSync('token'),
				userId:this.userId,
			},false)
			.then(res=>{
				console.log(res);
				this.list=res.obj.list;
				this.total=res.obj.total
			})
		},
		getLookerList(){
			this.xd_request_post(this.xdServerUrls.xd_getLookerByPushId,{
				pushId:this.pushId,
			},false)
			.then(res=>{
				console.log(res);
				this.lookerList=res.obj.list;
				this.lookTotal=res.obj.total
			})
		},
		
	},
	components:{
		actionlist
	}
}
